@import 'color';

$joyride-zindex: 900;
@import '../../node_modules/react-joyride/lib/styles/react-joyride';

.joyride {
    &-beacon {
        display: inline-block;
        height: $joyride-beacon-size;
        position: relative;
        width: $joyride-beacon-size;
        z-index: $joyride-zindex;

        &__inner {
            &:before {
                content: '?'
            }
            border-style: solid;
            background: transparent;
            color: $white;
            text-align: center;
        }

        &__outer {
            display: none;
        }
    }
}
