@import 'color';
@import 'constants';

.highcharts-container {
    width: 100% !important;

    svg {
        width: 100% !important;
    }
}

.highcharts-loading {
    background: transparent !important;

    > span {
        padding: 0.5em 1em;
        border-radius: $BORDER_RADIUS;
        color: $COLOR_INVERSE_TEXT !important;
        background: $COLOR_INVERSE_BG !important;

        .inverse & {
            color: $COLOR_INVERSE_BG !important;
            background: $COLOR_INVERSE_TEXT !important;
        }
    }
}

.inverse .trade-chart {
    color: $COLOR_INVERSE_TEXT;
}

.trade-chart,
.contract-chart {
    button {
        color: $COLOR_TEXT;

        &:hover {
            background: rgba($COLOR_BRAND_BLUE, 0.1);
        }
    }

    .inverse & button {
        color: $COLOR_INVERSE_TEXT;

        &:hover {
            background: rgba($COLOR_INVERSE_TEXT, 0.1);
        }
    }
}

@keyframes fadeIn {
    99% {
        visibility: hidden;
    }

    100% {
        visibility: visible;
    }
}

.trade-chart,
.contract-chart {
    .binary-chart-zoom-controls {
        visibility: hidden;
        animation: 2s fadeIn !important;
        animation-fill-mode: forwards;
        transition: visibility 2s;
        // opacity: 0;
    }

    &:hover .binary-chart-zoom-controls {
        visibility: visible;
        // opacity: 1;
    }
}

.trade-chart {
    display: flex;
    flex-direction: column;
    flex: 1;
    min-width: 0;
    min-height: 0;

    #trade-chart0,
    #trade-chart1,
    #trade-chart2,
    #trade-chart3,
    #trade-chart4 {
        flex: 1;
    }
}

.binary-chart-time-frame-picker {
    min-height: 24px;
}

.binary-chart-time-frame-button {
    background: rgba($COLOR_INVERSE_TEXT, 0.25) !important;
    padding: 0 !important;
}

.binary-chart-time-frame-button + .binary-chart-time-frame-button {
    margin-left: 2px;
}

.binary-chart-submenu {
    border-radius: $BORDER_RADIUS;
    background: $COLOR_BG;
    box-shadow: 0 0 1em 0 rgba(black, 0.5);
}

.inverse .binary-chart-submenu {
    background: $COLOR_INVERSE_BG;
}

.binary-chart-submenu button {
    color: $COLOR_TEXT;
    background: transparent;
    border-radius: 0;
}

.binary-chart-picker-item,
.binary-chart-button {
    text-align: left;
    padding: 0.1em;
    min-height: 1em;
    min-width: 1em;
    display: flex;
    align-items: center;

    svg + span {
        margin-left: 8px;
    }
}

.binary-chart-button,
.binary-chart-picker-item,
.binary-chart-time-frame-button {
    &:hover {
        box-shadow: 0 0 0 1px $COLOR_TEXT;
    }

    .inverse &:hover {
        box-shadow: 0 0 0 1px $COLOR_INVERSE_TEXT;
    }
}

.pressed {
    background: $COLOR_BRAND_BLUE !important;

    svg {
        fill: $COLOR_INVERSE_TEXT;
    }
}

.binary-chart-zoom-button {
    background: rgba($COLOR_BRAND_BLUE, 0.1) !important;

    svg {
        fill: $COLOR_BRAND_BLUE;
        margin-bottom: 4px;
    }

    &:hover {
        background: rgba($COLOR_BRAND_BLUE, 0.2) !important;
        box-shadow: 0 0 0 1px $COLOR_BRAND_BLUE;
    }
}

.inverse .binary-chart-zoom-button {
    background: rgba($COLOR_INVERSE_TEXT, 0.1) !important;

    svg {
        fill: $COLOR_INVERSE_TEXT;
    }

    &:hover {
        background: rgba($COLOR_INVERSE_TEXT, 0.2) !important;
        box-shadow: 0 0 0 1px $COLOR_INVERSE_TEXT;
    }
}

.binary-chart-toolbar {
    min-height: 30px;
    margin-bottom: 2px;
}

.binary-chart-toolbar > * {
    margin-right: 2px;
}

.binary-chart-button {
    background: rgba($COLOR_BRAND_BLUE, 0.1);
    border-radius: $BORDER_RADIUS;
    min-width: 1em;
    padding: 0.1em;
    flex: 1;
    align-items: center;
}

.binary-chart-button,
.binary-chart-picker-item {
    svg {
        width: 16px;
        height: 16px;
        fill: $COLOR_INVERSE_BG;
    }

    .inverse & svg {
        fill: $COLOR_BG;
    }
}

.binary-chart-info-bar {
    padding: 0.25em 0.5em;
    border-radius: $BORDER_RADIUS;
    background: rgba($COLOR_INVERSE_BG, 0.75);
    border: 1px solid $COLOR_INVERSE_BG;
    color: $COLOR_INVERSE_TEXT;

    .value-row {
        font-weight: bold;
    }

    .ohlc-row {
        text-transform: uppercase;
    }

    > div {
        line-height: 1.5;
        display: flex;

        > span {
            flex: 1;
        }
    }

    .inverse & {
        background: rgba($COLOR_BG, 0.75);
        border: 1px solid $COLOR_BG;
        color: $COLOR_TEXT;
    }
}

.highcharts-tooltip {
    display: none !important;
}
